<template>
    <div class="service-entry">
        <div class="service-entry-header">
            <div class="left service-entry-id">
                {{ id }}
            </div>
            <div>
                <button class="service-entry-button button right margin-left" @click="showDelete = !showDelete">
                    <font-awesome-icon icon="trash-alt"/>
                </button>
                <button class="service-entry-button button right" @click="editService(id)">
                    <font-awesome-icon icon="cog"/>
                </button>
            </div>
        </div>

        <ModalDialog v-if="showDelete" @cancel="showDelete = false" @ok="deleteService(id)">
            <p slot="header">Delete Service?</p>
            <p slot="body">Do you really want to delete service '{{id}}'?</p>
        </ModalDialog>

        <div class="service-entry-config" v-if="showEditService">
            <table>
                <tr>
                    <td><label>Category</label></td>
                    <td>{{ serviceDescription.serviceCategory.label }}</td>
                    <td/>
                </tr>
                <tr>
                    <td><label>Type</label></td>
                    <td>{{ serviceDescription.serviceType.label }}</td>
                    <td/>
                </tr>
                <template v-for="(param, index) in parameters"
                          v-bind:id="param.name"
                          v-bind:index="index">
                    <tr v-bind:key="param.name">
                        <td>
                            <label v-if="param.optional">{{formatString(param.name)}}</label>
                            <label v-if="!param.optional">{{formatString(param.name)}}*</label>
                        </td>
                        <td v-if="param.type == 'int'">
                            <input :type="param.secured ? 'password' : 'text'" autocomplete="off"
                                   v-model.number="parameterValues[index]"/>
                        </td>
                        <td v-if="param.type != 'int'">
                            <input :type="param.secured ? 'password' : 'text'" autocomplete="off"
                                   v-model.trim="parameterValues[index]"/>
                        </td>
                        <td>
                            <div v-if="!validationOk && parameterValidationErrors[index].length > 0"
                                 class="validationError">
                                {{parameterValidationErrors[index]}}
                            </div>
                        </td>
                    </tr>
                </template>
            </table>
            <div v-if="testInProgress" class="feedbackbox">
                <font-awesome-icon icon="spinner" class="fa-spin"/><label class="margin-left">Testing...</label>
            </div>
            <div v-if="testResult.length > 0" v-bind:class="testOk ? 'feedbackbox' : 'alertbox'">
                {{ testResult }}
            </div>
            <div v-if="saveResult.length > 0" v-bind:class="saveOk ? 'feedbackbox' : 'alertbox'">
                {{ saveResult }}
            </div>
            <button class="button right margin-left" @click="saveConfiguration()">
                <font-awesome-icon icon="save"/>
            </button>
            <button class="button right" @click="testConfiguration()">
                <font-awesome-icon icon="plug"/>
            </button>
        </div>

    </div>
</template>

<script>
    import ModalDialog from "../common/ModalDialog";
    export default {
        name: "ServiceListEntry",
        components: {ModalDialog},
        props: ['id', 'type'],
        data: function () {
            return {
                showDelete: false,
                showEditService: false,
                testResult: '',
                testInProgress: false,
                testOk: true,
                saveResult: '',
                saveOk: true,
                parameters: [],
                parameterValues: [],
                parameterValidationErrors: [],
                validationOk: true,
                serviceDescription: {},
                serviceConfiguration: {
                    id: '',
                    type: '',
                    parameters: {}
                }
            }
        },
        methods: {
            editService: function (id) {
                this.testResult = '';
                this.saveResult = '';
                let component = this;
                this.$http.get("/api/service/" + encodeURIComponent(id))
                    .then(function (response) {
                        component.serviceDescription = response.data;
                        component.loadTypeParameters(response.data.serviceType.type);
                        component.showEditService = !component.showEditService;
                    }).catch(function (error) {
                    console.log(error);
                })

            },
            deleteService: function (id) {
                this.showDelete = false;
                let component = this;
                this.$http.delete("/api/service/" + encodeURIComponent(id))
                    .then(function (response) {
                        component.$emit('service-deleted');
                    })
                    .catch(function (error) {
                        console.log(error);
                    })
            },
            loadTypeParameters: function (type) {
                let component = this;
                this.$http.get("/api/service/type/" + type)
                    .then(function (response) {
                        for (var i = component.parameters.length; i > 0; i--) {
                            component.parameters.pop();
                        }
                        Array.from(response.data).forEach(function (item, index) {
                            component.parameters.push(item)
                        })
                        component.fillTypeParameters();
                    }).catch(function (error) {
                    console.log(error);
                })
            },
            fillTypeParameters: function () {
                let component = this;
                this.parameters.forEach(function (param, index) {
                    if (component.serviceDescription.parameters[param.name] != null) {
                        component.parameterValues[index] = component.serviceDescription.parameters[param.name];
                    }
                })
            },
            testConfiguration: function () {
                this.validateInputs();
                if (!this.validationOk) {
                    return;
                }
                let component = this;
                component.testInProgress = true;
                component.serviceConfiguration.id = component.serviceDescription.id;
                component.serviceConfiguration.type = component.serviceDescription.serviceType.type;
                this.parameters.forEach(function (param, index) {
                    component.serviceConfiguration.parameters[param.name] = component.parameterValues[index];
                })
                this.$http.post("/api/service/test", this.serviceConfiguration
                ).then(function (response) {
                    component.testResult = response.data;
                    component.testOk = true;
                    component.testInProgress = false;
                }).catch(function (error) {
                    component.testResult = error.response.data;
                    component.testOk = false;
                    component.testInProgress = false;
                });
            },
            saveConfiguration: function () {
                this.validateInputs();
                if (!this.validationOk) {
                    return;
                }
                let component = this;
                component.serviceConfiguration.id = component.serviceDescription.id;
                component.serviceConfiguration.type = component.serviceDescription.serviceType.type;
                this.parameters.forEach(function (param, index) {
                    component.serviceConfiguration.parameters[param.name] = component.parameterValues[index];
                })
                this.$http.post("/api/service", this.serviceConfiguration
                ).then(function (response) {
                    component.saveResult = "Saved..."
                    component.saveOk = true;
                    component.showEditService = !component.showEditService;
                }).catch(function (error) {
                    component.saveResult = error.response.data;
                    component.saveOk = false;
                });
            },
            formatString: function (string) {
                string = string.replace(/\.?([A-Z])/g, function (x, y) {
                    return " " + y.toLowerCase()
                }).replace(/^_/, "");
                return string.charAt(0).toUpperCase() + string.slice(1)
            },
            validateInputs: function () {
                this.testResult = '';
                this.saveResult = '';
                this.validationOk = true;
                for (let i = this.parameterValidationErrors.length; i > 0; i--) {
                    this.parameterValidationErrors[i] = '';
                }
                let component = this;
                this.parameters.forEach(function (param, index) {
                    if (!param.optional && (component.parameterValues[index] == null || component.parameterValues[index] === "")) {
                        component.parameterValidationErrors[index] = "Value required";
                        component.validationOk = false;
                    }
                })
            },
        },
    }
</script>

<style scoped>

    .service-entry {
        padding: 15px 15px 15px 15px;
        background-color: var(--panel-background-color);
        overflow: auto;
        border-bottom: 5px solid var(--main-background-color);
    }

    .service-entry-config {
        margin-top: 40px;
    }

    .service-entry-id {
        margin-top: 2px;
        font-weight: bold;
    }

    .service-entry-button {
        margin-top: -1px;
    }

    .service-entry table tr {
        line-height: 30px;
    }

    .service-entry table td {
        text-align: left;
        padding-right: 15px;
    }

    .service-entry table tr td label {
        color: var(--font-color-light);
        margin-right: 15px;
    }

    .service-entry table tr td input, select {
        border: none;
        background-color: var(--element-background-color);
        height: 25px;
        min-width: 300px;
        color: var(--font-color-light);
    }

    .service-entry table tr td input {
        padding-left: 5px;
    }

    .service-entry table tr td select option {
        border: none;
        outline: none;
        background-color: var(--element-background-color);
        height: 25px;
    }

    .service-entry table tr td select:focus,
    .service-entry table tr td input:focus {
        background-color: var(--element-background-color-focus);
        color: var(--panel-background-color);
        outline: none;
    }

</style>