<template>
    <div class="job-entry">
        <div class="job-entry-header">
            <div class="left job-entry-id">
                {{ id }}
            </div>
            <div>
                <button class="job-entry-button button right margin-left" @click="deleteJob(id)">
                    <font-awesome-icon icon="trash-alt"/>
                </button>
                <button class="job-entry-button button right" @click="editJob(id)">
                    <font-awesome-icon icon="cog"/>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "JobListEntry",
        props: ['id'],
        data: function () {
            return {
            }
        },
        editService: function (id) {
            // TODO: Route to Job-Editor.
        },
        deleteService: function (id) {
            let component = this;
            this.$http.delete("/api/job/" + encodeURIComponent(id))
                .then(function (response) {
                    component.$emit('job-deleted');
                })
                .catch(function (error) {
                    console.log(error);
                })
        }
    }
</script>

