<template>
    <div class="panel">
        <div class="header-container overflow">
            <h1>Available Jobs</h1>
            <button class="button right" @click="showNewJob = !showNewJob">
                <font-awesome-icon icon="plus" v-if="showNewJob == false"/>
                <font-awesome-icon icon="minus" v-if="showNewJob == true"/>
            </button>
        </div>

        <JobListEntry v-for="(service, index) in services"
                          v-bind:id="service"
                          v-bind:index="index"
                          v-bind:key="service"
                          v-on:job-deleted="loadJobs()"/>
    </div>
</template>

<script>
    import JobListEntry from "./JobListEntry";
    export default {
        name: "JobList",
        components: {JobListEntry},
        data: function () {
            return {
                jobs: [],
                showNewJob: false,
            }
        },
        methods: {
            loadJobs: function () {
                let component = this;
                this.$http.get("/api/job/ids")
                    .then(function (response) {
                        for (let i = component.services.length; i > 0; i--) {
                            component.jobs.pop();
                        }
                        Array.from(response.data).forEach(function (item, index) {
                            component.jobs.push(item)
                        })
                        component.jobs.sort((a, b) => a.localeCompare(b));
                        component.showNewJob = false;
                    }).catch(function (error) {
                    console.log(error);
                });
            }
        },
        mounted() {
            this.loadJobs()
        }
    }
</script>

<style scoped>

    .header-container {
        padding: 15px 15px 15px 15px;
        border-bottom: 5px solid var(--main-background-color);
    }

</style>